'use client';

import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/navigation';
import { loadWishlist, addOrRemoveProductFromWishlist } from '../../_service/WishListService';
import { setProductData } from '../../_lib/utilReducer';
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { X } from "lucide-react";

const Wishlist = () => {
  const [products, setProducts] = useState([]);
  const [error, setError] = useState(null);
  const userId = useSelector((state) => state.session.user?.id);
  const token = useSelector((state) => state.session.token);

  useEffect(() => {
    const fetchWishlist = async () => {
      try {
        if (userId) {
          const wishlistProducts = await loadWishlist(userId, token);
          setProducts(wishlistProducts);
        }
      } catch (err) {
        setError('Failed to load wishlist. Please try again later.');
      }
    };

    fetchWishlist();
  }, [userId, token]);

  const handleRemoveProduct = async (productId) => {
    try {
      if (userId) {
        setProducts((prevProducts) => prevProducts.filter((product) => product.productId !== productId));
        await addOrRemoveProductFromWishlist(userId, productId, token);
      }
    } catch (err) {
      setError('Failed to remove product from wishlist. Please try again later.');
    }
  };

  return (
    <section className="bg-gray-50 max-w-full py-3 px-2">
      <div className="container mx-auto">
        <div className="mt-1 mx-2">
          <h1 className="text-3xl font-semibold text-gray-900">
            {products.length > 0 ? 'Your Wishlist' : 'Your Wishlist is empty'}
          </h1>
        </div>

        {error && (
          <div className="text-center text-red-500 mt-4">{error}</div>
        )}

        <div className="m-2 grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3">
          {products.map((product, index) => (
            <WishlistTile
              key={index}
              product={product.product}
              onRemoveProduct={handleRemoveProduct}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Wishlist;

function WishlistTile({ product, onRemoveProduct }) {
  const router = useRouter();
  const dispatch = useDispatch();

  const handleNavigation = () => {
    dispatch(setProductData({ id: product.id }));
    router.push(`/product/${product.name}`);
  };

  return (
    <Card className="overflow-hidden hover:scale-105 transition-transform">
      <div className="relative">
        <img className="h-48 w-full object-cover" src={product.image || "/_assets/image.png"} alt={product.name} />
        <Button
          variant="destructive"
          className="absolute top-2 right-2 p-1 rounded-full"
          onClick={() => onRemoveProduct(product.id)}
        >
          <X className="h-5 w-5" />
        </Button>
      </div>
      <CardHeader>
        <CardTitle className="cursor-pointer text-lg font-semibold" onClick={handleNavigation}>
          {product.name}
        </CardTitle>
      </CardHeader>
      <CardContent>
        <p className="text-sm text-gray-500">Brand: {product.brand}</p>
        <p className="text-sm text-gray-500">{product.description}</p>
        <div className="flex items-center space-x-2 mt-2">
          <p className="text-sm text-gray-500 line-through">${product.actualPrice}</p>
          <p className="text-lg text-orange-500 font-semibold">${product.offerPrice}</p>
          <p className="text-sm text-green-500">({product.discountPercentage}% off)</p>
        </div>
        <p className="text-sm text-gray-500">Rating: {product.rating} / 5</p>
      </CardContent>
    </Card>
  );
}
